 position: absolute;\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      content: '';\n      background: $dark;\n      opacity: 0;\n      pointer-events: none;\n      transition: opacity 0.3s;\n    }\n  }\n\n  &__image-thumb {\n    max-width: none;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: 50% 50%;\n  }\n\n  &__order {\n    width: 100%;\n    position: absolute;\n    top: calc(50% - 30px);\n    left: 0;\n    right: 0;\n    height: 60px;\n    pointer-events: none;\n    transition: opacity 0.3s;\n    opacity: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n\n  &__order-btn {\n    padding: 0;\n    width: 60px;\n    height: 60px;\n    border-radius: 15px;\n    background: #FFF;\n    margin: 0 5px;\n    pointer-events: all;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n  }\n\n  &__order-ico {\n    width: 30px;\n    height: 30px;\n    fill: $primary;\n  }\n\n  &__bottom {\n    background: #fbfbfb;\n    padding: 10px 30px 30px;\n    border-radius: 0 0 20px 20px;\n\n    @media(max-width: 575px) {\n      padding: 10px 20px 20px;\n    }\n  }\n\n  &__title {\n    display: block;\n    font-weight: 500;\n    font-size: 17px;\n    color: $primary;\n    margin: 0px 0 10px;\n    transition: color 0.3s;\n  }\n\n  &__category {\n    font-weight: 500;\n    font-size: 14px;\n    color: $primary;\n    opacity: 0.4;\n    margin: 0 0 10px;\n  }\n\n  &__code {\n    font-weight: 400;\n    font-size: 13px;\n    color: $primary;\n    margin: 0 0 10px;\n  }\n\n  &__cart {\n    margin-top: 20px;\n  }\n\n  &__amount {\n    display: inline-flex;\n    align-items: center;\n    background: #FFF;\n  }\n\n  &__amount-btn {\n    width: 36px;\n    height: 36px;\n    outline: none;\n    align-items: center;\n    display: inline-flex;\n    justify-content: center;\n    -webkit-appearance: none;\n    transition: all 0.18s linear;\n    border-radius: 8px;\n    background: #FFF;\n\n    &--disabled {\n      cursor: not-allowed;\n      #{$root} {\n        &__amount-ico {\n          fill: #999999;\n        }\n      }\n    }\n  }\n\n  &__amount-ico {\n    fill: $dark;\n    width: 12px;\n    height: 12px;\n    pointer-events: none;\n  }\n\n  &__amount-val {\n    font-size: 15px;\n    font-weight: 500;\n    text-align: center;\n    width: 50px;\n    height: 36px;\n    -webkit-appearance: none;\n    -moz-appearance: textfield;\n\n    &::-webkit-outer-spin-button,\n    &::-webkit-inner-spin-button {\n    -webkit-appearance: none;\n    }\n  }\n\n  &__price {\n    width: 100%;\n    display: flex;\n    align-items: flex-end;\n    flex-wrap: wrap;\n    margin: 20px 0 15px;\n\n    @media(max-width: 575px) {\n      margin: 15px 0;\n    }\n  }\n\n  &__current-price {\n    font-size: 20px;\n    color: $primary;\n    margin-right: 20px;\n\n    #{$root} {\n      &__price-label {\n        font-size: 18px;\n      }\n    }\n\n    @media(max-width: 575px) {\n      font-size: 18px;\n      margin-right: 15px;\n    }\n  }\n\n  &__price-val {\n    font-weight: bold;\n    margin-right: 5px;\n  }\n\n  &__old-price {\n    font-size: 16px;\n    color: $primary;\n    opacity: .2;\n    text-decoration: line-through;\n    #{$root} {\n      &__price-label {\n        font-size: 15px;\n\n        @media(max-width: 575px) {\n          font-size: 14px;\n        }\n      }\n    }\n\n    @media(max-width: 575px) {\n      font-size: 15px;\n    }\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
