-width: 550px;\n    margin-right: 50px;\n    position: relative;\n\n    @media(max-width: 1740px) {\n      max-width: 450px;\n      margin-right: 25px;\n    }\n\n    @media(max-width: 1440px) {\n      max-width: 100%;\n      margin-right: 0;\n      order: 1;\n    }\n  }\n\n  &__tags {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    position: absolute;\n    top: 30px;\n    left: 20px;\n    z-index: 1;\n    @media(max-width: 767px) {\n      top: 15px;\n      left: 15px;\n    }\n  }\n\n  &__tag {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    color: #FFF;\n    font-size: 13px;\n    font-weight: 500;\n    padding: 7px 15px;\n    border-radius: 7px;\n    text-align: center;\n    margin-right: 9px;\n\n    &--new {\n      background: #26c582;\n    }\n\n    &--sale {\n      background: #f94183;\n    }\n\n    &:last-child {\n      margin-right: 0;\n    }\n  }\n\n  &__slider-current {\n    width: 100%;\n    position: relative;\n    overflow: hidden;\n    border-radius: 8px;\n    margin-bottom: 10px;\n\n    &::after {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n  }\n\n  &__slider-image {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n  }\n\n  &__slider-additional {\n    display: flex;\n    flex-wrap: wrap;\n    margin: 0 -10px;\n  }\n\n  &__slider-thumb {\n    width: 100%;\n    max-width: calc(25% - 20px);\n    margin: 10px;\n    border-radius: 10px;\n    overflow: hidden;\n    position: relative;\n    cursor: pointer;\n\n    &::before {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n\n    &::after {\n      position: absolute;\n      content: \"\";\n      top: 0;\n      left: 0;\n      right: 0;\n      bottom: 0;\n      background: $dark;\n      opacity: 0;\n      pointer-events: none;\n      z-index: 1;\n    }\n\n    &--active::after {\n      opacity: .3;\n    }\n  }\n\n  &__slider-thumb-image {\n    position: absolute;\n    left: 0;\n    top: 0;\n    max-width: none;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: 50% 50%;\n  }\n\n  &__content {\n    width: 100%;\n    max-width: calc(100% - 600px);\n\n    @media(max-width: 1740px) {\n      max-width: calc(100% - 475px);\n    }\n\n    @media(max-width: 1440px) {\n      max-width: 100%;\n      order: 0;\n    }\n  }\n\n  &__title {\n    font-weight: bold;\n    font-size: 40px;\n    color: $primary;\n    margin-bottom: 8px;\n\n    @media(max-width: 1740px) {\n      font-size: 35px;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 30px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 25px;\n      margin-bottom: 5px;\n    }\n  }\n\n  &__categories-list {\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  &__categories-link {\n    font-weight: normal;\n    font-size: 15px;\n    line-height: 18px;\n    color: $primary;\n    opacity: 0.5;\n    margin-right: 10px;\n\n    &:last-child {\n      margin-right: 0;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n      margin-right: 5px;\n    }\n  }\n\n  &__code {\n    font-weight: 400;\n    font-size: 13px;\n    color: $dark;\n    margin: 20px 0;\n\n    @media(max-width: 767px) {\n      margin: 15px 0;\n    }\n  }\n\n  &__props {\n\n    &--full {\n      @media(max-width: 1440px) {\n        display: none;\n      }\n    }\n\n    &--mobile {\n      display: none;\n      @media(max-width: 1440px) {\n        margin-top: 30px;\n        display: block;\n      }\n    }\n  }\n\n  &__props-row {\n    display: flex;\n    align-items: center;\n    flex-wrap: wrap;\n    margin-bottom: 30px;\n    @media(max-width: 767px) {\n      margin-bottom: 20px;\n    }\n  }\n\n  &__props-label {\n    font-size: 15px;\n    color: $primary;\n    opacity: 0.6;\n    padding-right: 25px;\n    min-width: 100px;\n  }\n\n  &__props-val {\n    font-weight: 500;\n    font-size: 15px;\n    color: $primary;\n  }\n\n  &__entry {\n    width: 100%;\n    max-width: 100%;\n\n    &--desktop {\n      @media(max-width: 1740px) {\n        display: none;\n      }\n    }\n\n    &--mobile {\n      display: none;\n      @media(max-width: 1740px) {\n        display: block;\n      }\n    }\n  }\n\n  &__entry-title {\n    color: $dark;\n    font-weight: 600;\n    font-size: 20px;\n    margin-bottom: 30px;\n\n    @media(max-width: 767px) {\n      font-size: 18px;\n      margin-bottom: 20px;\n    }\n  }\n\n  &__entry-descr , &__entry-descr p {\n    font-weight: normal;\n    font-size: 16px;\n    color: $dark2;\n    line-height: 1.7;\n\n    p {\n      margin-bottom: 15px;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 14px;\n    }\n  }\n\n  &__bottom {\n    width: 100%;\n    max-width: 100%;\n    @media(max-width: 1440px) {\n      order: 3;\n    }\n  }\n\n  &__order {\n    display: none;\n    margin: 30px 0;\n\n    @media(max-width: 991px) {\n      display: block;\n    }\n  }\n\n  &__order-row {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n  }\n\n  &__folders {\n    margin-bottom: 30px;\n\n    @media(max-width: 767px) {\n      margin-bottom: 20px;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  &__folders-title {\n    color: $dark;\n    font-weight: 400;\n    font-size: 22px;\n    margin-bottom: 20px;\n\n    @media(max-width: 767px) {\n      font-size: 18px;\n      margin-bottom: 15px;\n    }\n  }\n\n  &__folders-list {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n  }\n\n  &__folders-link {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 1.2;\n    color: #fff;\n    background: $dark;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    margin: 0 10px 10px 0;\n    padding: 10px 20px 9px;\n    border-radius: 10px;\n\n    @media(max-width: 767px) {\n      padding: 5px 10px;\n      border-radius: 5px;\n      margin: 0 5px 5px 0;\n      font-size: 13px;\n    }\n  }\n\n  &__form {\n    width: 100%;\n    max-width: 405px;\n    padding: 50px 50px 50px;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    background: #FFF;\n    width: 100%;\n    border-radius: 20px;\n\n    @media(max-width: 1199px) {\n      max-width: 350px;\n      padding: 40px 30px 30px;\n    }\n\n    @media(max-width: 991px) {\n      display: none;\n    }\n  }\n\n  &__form-title {\n    color: $dark;\n    font-weight: 700;\n    font-size: 30px;\n    margin-bottom: 20px;\n\n    @media(max-width: 1199px) {\n      font-size: 25px;\n      margin-bottom: 15px;\n    }\n  }\n\n  &__form-tag {\n    background: rgba(41, 194, 158, 0.15);\n    border-radius: 7px;\n    font-weight: 500;\n    font-size: 13px;\n    color: #29c29e;\n    padding: 7px 14px 7px 37px;\n    display: inline-flex;\n    position: relative;\n    margin-bottom: 30px;\n    @media(max-width: 1199px) {\n      margin-bottom: 20px;\n    }\n  }\n\n  &__form-tag-ico {\n    color: #29C29E;\n    width: 12px;\n    height: 12px;\n    position: absolute;\n    left: 14px;\n    top: calc(50% - 6px);\n  }\n\n  &__price {\n    width: 100%;\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    margin: 20px 0;\n\n    @media(max-width: 991px) {\n      margin: 0;\n      height: 36px;\n    }\n\n    @media(max-width: 410px) {\n      height: auto;\n    }\n  }\n\n  &__current-price {\n    font-size: 24px;\n    color: $primary;\n    margin-right: 20px;\n\n    #{$root} {\n      &__price-label {\n        font-size: 20px;\n      }\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 22px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 18px;\n      margin-right: 15px;\n    }\n  }\n\n  &__price-val {\n    font-weight: bold;\n    margin-right: 5px;\n  }\n\n  &__old-price {\n    font-size: 20px;\n    color: $primary;\n    opacity: .2;\n    text-decoration: line-through;\n    #{$root} {\n      &__price-val {\n        font-weight: 500;\n      }\n      &__price-label {\n        font-size: 18px;\n\n        @media(max-width: 575px) {\n          font-size: 14px;\n        }\n      }\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 18px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 16px;\n    }\n  }\n\n  &__add {\n    margin-top: 30px;\n    width: 100%;\n    display: inline-flex;\n    justify-content: space-between;\n    align-items: center;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 1.2;\n    padding: 20px 30px;\n    color: #fff;\n    border: none;\n    background: $dark;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    transition: all 0.18s linear;\n    border-radius: 10px;\n\n    &:hover, &:focus {\n      background: $dark2;\n    }\n\n    @media(max-width: 991px) {\n      max-width: 300px;\n    }\n  }\n\n  &__add-ico {\n    fill: #FFF;\n    width: 21px;\n    height: 21px;\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
