ddress {\n    width: 100%;\n    max-width: calc(100% - 300px);\n    padding: 0 20px 0 30px;\n    position: relative;\n\n    @media(max-width: 1440px) {\n      max-width: calc(100% - 215px);\n      padding: 0 10px 0 20px;\n    }\n\n    @media(max-width: 1199px) {\n      max-width: 250px;\n    }\n\n    @media(max-width: 991px) {\n      max-width: 230px;\n    }\n\n    &--top {\n      @media(max-width: 767px) {\n        display: none;\n      }\n      #{$root} {\n        &__address-text {\n          @media(max-width: 1440px) {\n            font-size: 14px;\n          }\n          @media(max-width: 1199px) {\n            font-size: 16px;\n          }\n        }\n      }\n    }\n  }\n\n  &__address-ico {\n    position: absolute;\n    left: 0;\n    top: 5px;\n    width: 9px;\n    height: 18px;\n    fill: $primary;\n  }\n\n  &__address-text {\n    color: $primary;\n    font-size: 16px;\n  }\n\n  &__phone {\n    width: 100%;\n    max-width: 280px;\n    padding: 0 20px 0 30px;\n    position: relative;\n    @media(max-width: 1440px) {\n      max-width: 195px;\n      padding: 0 10px 0 20px;\n    }\n\n    @media(max-width: 1199px) {\n      max-width: 250px;\n    }\n\n    @media(max-width: 991px) {\n      max-width: 240px;\n    }\n\n    &--top {\n      @media(max-width: 767px) {\n        display: none;\n      }\n      #{$root} {\n        &__phone-link {\n          @media(max-width: 1440px) {\n            font-size: 16px;\n          }\n          @media(max-width: 1199px) {\n            font-size: 20px;\n          }\n        }\n      }\n    }\n\n  }\n\n  &__phone-ico {\n    position: absolute;\n    left: 0;\n    top: 5px;\n    width: 16px;\n    height: 16px;\n    fill: $primary;\n  }\n\n  &__phone-link {\n    color: $primary;\n    font-weight: bold;\n    font-size: 21px;\n    display: block;\n  }\n\n  &__more {\n    width: 100%;\n    max-width: 20px;\n    height: 20px;\n    position: relative;\n    z-index: 5;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    cursor: pointer;\n\n    @media(max-width: 767px) {\n      display: none;\n    }\n  }\n\n  &__more-ico {\n    color: $light;\n    width: 100%;\n    height: 100%;\n  }\n\n  &__popup {\n    width: 100%;\n    max-width: 405px;\n    background: $dark;\n    position: absolute;\n    border-radius: 20px;\n    padding: 30px 40px 40px;\n    right: 0;\n    top: 100%;\n    z-index: 5;\n\n    @media(max-width: 767px) {\n      top: 0;\n      left: 0;\n      max-width: 100%;\n      position: fixed;\n      z-index: 1030;\n      border-radius: 0;\n      padding: 30px 20px;\n      height: 100%;\n    }\n\n    #{$root} {\n      &__phone,\n      &__address {\n        max-width: 100%;\n        padding: 0 0 0 40px;\n        margin-bottom: 20px;\n      }\n      &__phone-link {\n        color: #FFF;\n      }\n      &__address-text {\n        font-size: 17px;\n        color: #FFF;\n      }\n      &__address-ico,\n      &__phone-ico {\n        fill: #FFF;\n      }\n    }\n  }\n\n  &__popup-title {\n    color: #FFF;\n    font-weight: bold;\n    font-size: 26px;\n    padding: 0 25px 25px 0;\n  }\n\n  &__popup-btn {\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    top: 35px;\n    right: 40px;\n  }\n\n  &__popup-close {\n    fill: #fff;\n    width: 100%;\n    height: 100%;\n  }\n\n  &__social {\n    width: 100%;\n    padding: 0 5px;\n  }\n\n  &__social-list {\n    margin: 0 -5px;\n    display: flex;\n    align-items: center;\n  }\n\n  &__social-link {\n    border-radius: 5px;\n    width: 100%;\n    max-width: calc(16.666% - 10px);\n    margin: 0 5px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    position: relative;\n    background: $dark2;\n    transition: background .3s ease;\n\n    &:hover {\n      background: $shadow-dark2;\n    }\n\n    &::after {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n\n    @media(max-width: 767px) {\n      max-width: 40px;\n    }\n  }\n\n  &__social-ico {\n    fill: #FFF;\n    max-width: 20px;\n    max-height: 21px;\n    position: absolute;\n  }\n\n  &__btn {\n    margin-top: 30px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    padding: 11px 20px 11px;\n    font-size: 15px;\n    color: #FFF;\n    font-weight: 500;\n    border-radius: 10px;\n    background: $dark2;\n    transition: background .3s ease;\n\n    &:hover, &:focus {\n      background: $shadow-dark2;\n    }\n  }\n\n  &__form-title {\n    font-weight: bold;\n    font-size: 26px;\n    color: #fff;\n    padding: 0 30px 40px 0;\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
